import React, { Component } from 'react';
import tweet from './tweet.css';

class Tweet extends Component {
    constructor(props){
        super(props);
        this.tweetId = React.createRef();

    }
    componentWillMount=()=>{
        console.log(this.props.likes);
  
      }
    addLikes=()=>{
        const url = `api/v1/tweets/like/${this.props.id}`;
        fetch(url)
        .then(response=>response.json())
        .then(data=>console.log(data))
      }
    render() { 
        return ( 
            <div className="tweet">
                <img src={this.props.src}/>
                <p>{this.props.username}</p>
                <p>
                {this.props.tweet}
                </p>
                <p>{this.props.likes.length}</p>
                <span className={this.props.likes.length<1?"likes fa fa-heart":"likes fa fa-heart red-heart"} aria-hidden="true" onClick={this.addLikes}>{this.props.likes} likes</span>
                <span className="retweets">{this.props.retweets} retweets</span>
                <span className="comments">{this.props.comments} comments</span>

            </div>
         );
    }
}
 
export default Tweet;